.input:focus {
	outline: none;
}

.input {
	width: 372px;
	padding: 8px 12px;
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 20px;
	border: 1px solid #d1d1d1;
	border-radius: 5px;
	/* identical to box height */

	color: #000000;
}

.inputWrapper {
	position: relative;
	/* display: flex;
	flex-direction: column-reverse;
	gap: 5px; */
}
.inputWrapper span {
	display: block;
	position: absolute;
	font-size: 10px;
	line-height: 12px;
	top: -17px;
	color: #cc1439;
}

